t\t\t<div class=\"file-info\">\n\t\t\t\t\t<span class=\"filename\">{{ file.filename }}</span>\n\t\t\t\t\t<span class=\"owner\">{{ file.fileowner }}</span>\n\t\t\t\t\t<span class=\"size\">{{ formatBytes(Number(file.filesize)) }}</span>\n\t\t\t\t\t<span class=\"delete-file-btn icon-delete-white\" @click=\"deleteGroupFile(file)\" />\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t</div>\n\t</div>\n\t<div v-else>\n\t\t<span class=\"icon-loading\" style=\"width: 20px; height: 20px; display: flex; margin: 20px auto;\" />\n\t</div>\n</template>\n\n<script>\nimport axios from '@nextcloud/axios'\nimport { generateUrl } from '@nextcloud/router'\nimport Formats from '../../mixins/Formats'\nimport { mapGetters } from 'vuex'\nimport DetailsFile from './DetailsFile'\nimport { emit } from '@nextcloud/event-bus'\nimport { showError, showWarning } from '@nextcloud/dialogs'\n\nexport default {\n\tname: 'DetailsGroupList',\n\tcomponents: { DetailsFile },\n\tmixins: [Formats],\n\tprops: {\n\t\tfiles: {\n\t\t\ttype: Array,\n\t\t\trequired: true,\n\t\t},\n\t\tloadingFiles: {\n\t\t\ttype: Boolean,\n\t\t\trequired: true,\n\t\t},\n\t},\n\tcomputed: {\n\t\t...mapGetters([\n\t\t\t'detailsGridSize',\n\t\t]),\n\t},\n\tmethods: {\n\t\tdeleteGroupFile(file) {\n\t\t\tif (confirm(t('mediadc', 'Are you sure, you want delete this file?'))) {\n\t\t\t\taxios.delete(generateUrl(`/apps/mediadc/api/v1/tasks/${file.taskId}/files/${file.detailId}/${file.fileid}`))\n\t\t\t\t\t.then(res => {\n\t\t\t\t\t\tif (res.data.success) {\n\t\t\t\t\t\t\tconst files = this.files\n\t\t\t\t\t\t\tconst fileidIndex = files.findIndex(f => f.fileid === file.fileid)\n\t\t\t\t\t\t\tfiles.splice(fileidIndex, 1)\n\t\t\t\t\t\t\tthis.$emit('update:files', files)\n\t\t\t\t\t\t\temit('updateTaskInfo')\n\t\t\t\t\t\t\tthis.$store.dispatch('setTask', res.data.task)\n\t\t\t\t\t\t} else if ('locked' in res.data && res.data.locked) {\n\t\t\t\t\t\t\tshowWarning(t('mediadc', 'Wait until file loaded before deleting'))\n\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\tshowError(t('mediadc', 'Some error occured while deleting file'))\n\t\t\t\t\t\t}\n\t\t\t\t\t})\n\t\t\t\t\t.catch(err => {\n\t\t\t\t\t\tconsole.debug(err)\n\t\t\t\t\t\tshowError(t('mediadc', 'Some error occured while deleting file'))\n\t\t\t\t\t})\n\t\t\t}\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.details-group {\n\twidth: 100%;\n\tborder-top: 1px solid #dadada;\n\tborder-bottom: 1px solid #dadada;\n\tmargin: 10px 0;\n}\n\nbody.theme--dark .details-group {\n\tborder-color: #717171;\n}\n\n.details-group-files {\n\tdisplay: flex;\n\tflex-wrap: wrap;\n\tjustify-content: center;\n\tmax-height: 90vh;\n\toverflow-y: scroll;\n}\n\n.file {\n\tdisplay: flex;\n\theight: 100%;\n\tflex-direction: column;\n\tjustify-content: space-between;\n\twidth: 192px;\n\tborder-radius: 5px;\n\tborder: 1px solid #dadada;\n\ttransition: box-shadow .3s;\n\tmargin: 10px;\n}\n\nbody.theme--dark .file {\n\tborder-color: #717171;\n}\n\n@media (max-width: 540px) {\n\t.file {\n\t\tmax-width: 192px;\n\t}\n\n\t.pagination {\n\t\ttext-align: center;\n\t}\n}\n\n.file:hover {\n\tbox-shadow: 0 0 10px 0 rgba(0, 0, 0, .25);\n}\n\n.file-info {\n\twidth: 100%;\n\tpadding: 5px 10px;\n\tdisplay: flex;\n\tflex-direction: column;\n\talign-items: center;\n\tbackground-color: #000;\n\ttext-align: center;\n\tborder-bottom-left-radius: 5px;\n\tborder-bottom-right-radius: 5px;\n\tcolor: #fff;\n}\n\n.filename {\n\twidth: 100%;\n\toverflow-x: hidden;\n\ttext-overflow: ellipsis;\n\twhite-space: nowrap;\n}\n\n.delete-file-btn {\n\tpadding: 20px;\n\tborder-radius: 50%;\n\twidth: 16px;\n\theight: 16px;\n\tcursor: pointer;\n\tbackground-image: var(--icon-delete-fff);\n}\n\n.delete-file-btn:hover {\n\tbackground-color: #eee;\n}\n\n.delete-file-btn:active {\n\tbackground-color: #ddd;\n}\n\nbody.theme--dark .delete-file-btn {\n\tbackground-image: var(--icon-delete-000);\n}\n\n.delete-file-btn:hover, body.theme--dark .delete-file-btn:hover {\n\tbackground-image: var(--icon-delete-e9322d);\n}\n</style>\n"],"sourceRoot":""}]);
