___CSS_LOADER_EXPORT___.push([module.id, "\n.container[data-v-8fd3420e] {\n\tpadding: 0 20px;\n\twidth: 100%;\n\tmax-width: 1440px;\n\tmargin: 0 auto;\n\tmax-height: 100%;\n}\n@media (min-width: 1920px) {\n.container[data-v-8fd3420e] {\n\t\tmax-width: 80vw;\n}\n}\nh2[data-v-8fd3420e] {\n\tdisplay: inline-flex;\n\talign-items: center;\n\tjustify-content: center;\n\twidth: 100%;\n\tmargin: 20px auto;\n\ttext-align: center;\n}\n.collapse-task-status-btn[data-v-8fd3420e] {\n\tdisplay: inline-flex;\n\tcursor: pointer;\n\twidth: 16px;\n\theight: 16px;\n\tmargin: 0 10px;\n\tpadding: 20px;\n\tborder-radius: 50%;\n\tuser-select: none;\n}\n.task-details-heading p[data-v-8fd3420e] {\n\ttext-align: center;\n}\n.details-row[data-v-8fd3420e] {\n\tdisplay: flex;\n\tmargin: 0 10px 20px;\n}\n@media (max-width: 767px) {\n.details-row[data-v-8fd3420e] {\n\t\tflex-wrap: wrap;\n}\n.task-details-heading[data-v-8fd3420e] {\n\t\tflex-direction: column;\n}\n}\n.task-status-row[data-v-8fd3420e] {\n\tdisplay: flex;\n\tjustify-content: center;\n\talign-items: center;\n\tflex-wrap: wrap;\n}\n.task-status[data-v-8fd3420e] {\n\tdisplay: inline-flex;\n\talign-items: center;\n\tborder: 1px solid #dadada;\n\tborder-radius: 5px;\n\tmargin: 20px;\n\tpadding: 10px;\n}\n.actions-menu-button[data-v-8fd3420e] {\n\tpadding: 20px;\n\tborder-radius: 50%;\n\tcursor: pointer;\n\tuser-select: none;\n}\n.actions-menu-button[data-v-8fd3420e]:hover, .collapse-task-status-btn[data-v-8fd3420e]:hover {\n\tbackground-color: #eee;\n}\n.actions-menu-button[data-v-8fd3420e]:active, .collapse-task-status-btn[data-v-8fd3420e]:active {\n\tbackground-color: #ddd;\n}\nbody.theme--dark .actions-menu-button[data-v-8fd3420e]:hover, body.theme--dark .collapse-task-status-btn[data-v-8fd3420e]:hover {\n\tbackground-color: #727272;\n}\nbody.theme--dark .actions-menu-button[data-v-8fd3420e]:active, body.theme--dark .collapse-task-status-btn[data-v-8fd3420e]:active {\n\tbackground-color: #5b5b5b;\n}\nbody.theme--dark .task-status[data-v-8fd3420e], body.theme--dark .task-info[data-v-8fd3420e] {\n\tborder-color: #717171;\n}\n.task-info[data-v-8fd3420e] {\n\tmargin: 20px 0;\n\tborder: 1px solid #dadada;\n\tborder-radius: 5px;\n\tpadding: 10px 20px;\n\theight: 100%;\n\tmax-height: 94px;\n\tmax-width: 50%;\n\toverflow-y: scroll;\n}\n.owner-tooltip[data-v-8fd3420e] {\n\tposition: relative;\n}\n.tooltip-title[data-v-8fd3420e] {\n\tdisplay: none;\n\tpadding: 0 5px;\n\tborder-radius: 5px;\n\tbackground-color: #000;\n\tcolor: #fff;\n\tposition: absolute;\n\ttop: calc(-100% - 5px);\n\tleft: 50%;\n\ttransform: translateX(-50%);\n\tfont-size: 12px;\n\tbox-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);\n}\n.tooltip-title[data-v-8fd3420e]:before {\n\tcontent: '';\n\tposition: absolute;\n\tleft: 50%;\n\ttransform: translateX(-50%) rotateZ(45deg);\n\tbottom: -3px;\n\twidth: 10px;\n\theight: 10px;\n\tbackground-color: #000;\n\tz-index: -1;\n}\n.owner-tooltip:hover .tooltip-title[data-v-8fd3420e] {\n\tdisplay: block;\n}\n@media (max-width: 767px) {\n.details-row[data-v-8fd3420e] {\n\t\tmargin: 0 0 20px;\n}\n.task-status[data-v-8fd3420e] {\n\t\tflex-direction: column;\n\t\tmargin: 0 0 20px;\n\t\twidth: 100%;\n}\n.task-info[data-v-8fd3420e] {\n\t\tmax-width: 100%;\n\t\tmargin: 0;\n}\n}\n.task-info h3[data-v-8fd3420e] {\n\tmargin: 0 0 5px 0;\n}\n.target-directory-row[data-v-8fd3420e] {\n\twhite-space: nowrap;\n}\n.badge[data-v-8fd3420e] {\n\tdisplay: inline-flex;\n\tpadding: 0 10px;\n\tbackground-color: #eee;\n\tborder-radius: 20px;\n\tmargin-right: 20px;\n}\n@media (max-width: 540px) {\n.task-status-row[data-v-8fd3420e] {\n\t\tflex-direction: column;\n\t\tmargin: 20px 0;\n}\n.badge[data-v-8fd3420e] {\n\t\tmargin-right: 0;\n\t\tmargin-top: 10px;\n\t\tmargin-bottom: 10px;\n}\n}\n.badge.finished[data-v-8fd3420e] {\n\tbackground-color: #49b382;\n\tcolor: #fff;\n}\n.badge.running[data-v-8fd3420e], .badge.pending[data-v-8fd3420e] {\n\tbackground-color: #dadada;\n\tcolor: #000;\n}\n.badge.error[data-v-8fd3420e] {\n\tbackground-color: #bd3f3f;\n\tcolor: #fff;\n}\n.badge.terminated[data-v-8fd3420e] {\n\tbackground-color: #f17b1b;\n\tcolor: #fff;\n}\n.errors[data-v-8fd3420e] {\n\tmargin: 20px;\n\tborder: 1px solid #bd3f3f;\n\tborder-radius: 5px;\n\tpadding: 10px;\n\tmax-height: 100vh;\n\toverflow-y: scroll;\n}\n.error-row[data-v-8fd3420e] {\n\tborder-bottom: 1px solid #bd3f3f;\n\tpadding: 5px 0;\n\tmax-height: 100%;\n\toverflow-y: scroll;\n}\n", "",{"version":3,"sources":["webpack://src/views/CollectorDetails.vue"],"names":[],"mappings":";AA8TA;CACA,eAAA;CACA,WAAA;CACA,iBAAA;CACA,cAAA;CACA,gBAAA;AACA;AAEA;AACA;EACA,eAAA;AACA;AACA;AAEA;CACA,oBAAA;CACA,mBAAA;CACA,uBAAA;CACA,WAAA;CACA,iBAAA;CACA,kBAAA;AACA;AAEA;CACA,oBAAA;CACA,eAAA;CACA,WAAA;CACA,YAAA;CACA,cAAA;CACA,aAAA;CACA,kBAAA;CACA,iBAAA;AACA;AAEA;CACA,kBAAA;AACA;AAEA;CACA,aAAA;CACA,mBAAA;AACA;AAEA;AACA;EACA,eAAA;AACA;AACA;EACA,sBAAA;AACA;AACA;AAEA;CACA,aAAA;CACA,uBAAA;CACA,mBAAA;CACA,eAAA;AACA;AAEA;CACA,oBAAA;CACA,mBAAA;CACA,yBAAA;CACA,kBAAA;CACA,YAAA;CACA,aAAA;AACA;AAEA;CACA,aAAA;CACA,kBAAA;CACA,eAAA;CACA,iBAAA;AACA;AAEA;CACA,sBAAA;AACA;AAEA;CACA,sBAAA;AACA;AAEA;CACA,yBAAA;AACA;AAEA;CACA,yBAAA;AACA;AAEA;CACA,qBAAA;AACA;AAEA;CACA,cAAA;CACA,yBAAA;CACA,kBAAA;CACA,kBAAA;CACA,YAAA;CACA,gBAAA;CACA,cAAA;CACA,kBAAA;AACA;AAEA;CACA,kBAAA;AACA;AAEA;CACA,aAAA;CACA,cAAA;CACA,kBAAA;CACA,sBAAA;CACA,WAAA;CACA,kBAAA;CACA,sBAAA;CACA,SAAA;CACA,2BAAA;CACA,eAAA;CACA,yCAAA;AACA;AAEA;CACA,WAAA;CACA,kBAAA;CACA,SAAA;CACA,0CAAA;CACA,YAAA;CACA,WAAA;CACA,YAAA;CACA,sBAAA;CACA,WAAA;AACA;AAEA;CACA,cAAA;AACA;AAEA;AACA;EACA,gBAAA;AACA;AAEA;EACA,sBAAA;EACA,gBAAA;EACA,WAAA;AACA;AAEA;EACA,eAAA;EACA,SAAA;AACA;AACA;AAEA;CACA,iBAAA;AACA;AAEA;CACA,mBAAA;AACA;AAEA;CACA,oBAAA;CACA,eAAA;CACA,sBAAA;CACA,mBAAA;CACA,kBAAA;AACA;AAEA;AACA;EACA,sBAAA;EACA,cAAA;AACA;AAEA;EACA,eAAA;EACA,gBAAA;EACA,mBAAA;AACA;AACA;AAEA;CACA,yBAAA;CACA,WAAA;AACA;AAEA;CACA,yBAAA;CACA,WAAA;AACA;AAEA;CACA,yBAAA;CACA,WAAA;AACA;AAEA;CACA,yBAAA;CACA,WAAA;AACA;AAEA;CACA,YAAA;CACA,yBAAA;CACA,kBAAA;CACA,aAAA;CACA,iBAAA;CACA,kBAAA;AACA;AAEA;CACA,gCAAA;CACA,cAAA;CACA,gBAAA;CACA,kBAAA;AACA","sourcesContent":["<!--\n - @copyright Copyright (c) 2021 Andrey Borysenko <andrey18106x@gmail.com>\n -\n - @copyright Copyright (c) 2021 Alexander Piskun <bigcat88@icloud.com>\n -\n - @author Andrey Borysenko <andrey18106x@gmail.com>\n -\n - @license AGPL-3.0-or-later\n -\n - This program is free software: you can redistribute it and/or modify\n - it under the terms of the GNU Affero General Public License as\n - published by the Free Software Foundation, either version 3 of the\n - License, or (at your option) any later version.\n -\n - This program is distributed in the hope that it will be useful,\n - but WITHOUT ANY WARRANTY; without even the implied warranty of\n - MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE. See the\n - GNU Affero General Public License for more details.\n -\n - You should have received a copy of the GNU Affero General Public License\n - along with this program. If not, see <http://www.gnu.org/licenses/>.\n -\n -->\n\n<template>\n\t<div v-if=\"!loading\" class=\"container\">\n\t\t<TasksEdit v-if=\"editingTask\" :opened.sync=\"editingTask\" />\n\t\t<div class=\"task-details\">\n\t\t\t<div class=\"task-details-heading\">\n\t\t\t\t<h2>\n\t\t\t\t\t{{ rootTitle }}\n\t\t\t\t\t<span :class=\"!collapsedStatus\n\t\t\t\t\t\t\t? 'icon-triangle-n collapse-task-status-btn'\n\t\t\t\t\t\t\t: 'icon-triangle-s collapse-task-status-btn'\"\n\t\t\t\t\t\t@click=\"collapseTaskStatus\" />\n\t\t\t\t</h2>\n\t\t\t\t<div v-show=\"!collapsedStatus\" class=\"task-details-description\">\n\t\t\t\t\t<p>\n\t\t\t\t\t\t{{ t('mediadc', 'Here you can view task details, manage task (stop or restart), ' +\n\t\t\t\t\t\t\t'delete found duplicated photos and videos.') }}\n\t\t\t\t\t</p>\n\t\t\t\t\t<p>\n\t\t\t\t\t\t{{ t('mediadc', 'Deleted files are placed in the trash, so that they can be restored in case of need.') }}\n\t\t\t\t\t</p>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t\t<div v-show=\"!collapsedStatus\" class=\"task-status-row\">\n\t\t\t\t<div class=\"task-status\">\n\t\t\t\t\t<span :class=\"'badge ' + getStatusBadge(task)\">{{ getStatusBadge(task) }}</span>\n\t\t\t\t\t<div style=\"display: flex; flex-direction: column;\">\n\t\t\t\t\t\t<span>\n\t\t\t\t\t\t\t<b>{{ parseTargetMtype(task) }}</b> {{ task.files_scanned !== task.files_total ? `${task.files_scanned}/` : '' }}{{ task.files_total }} file(s)\n\t\t\t\t\t\t\t({{ formatBytes(Number(task.files_total_size)) }})\n\t\t\t\t\t\t\t({{ task !== null && 'collector_settings' in task ? t('mediadc', 'precision: ') + JSON.parse(task.collector_settings).similarity_threshold + '%' : '' }})\n\t\t\t\t\t\t\t<br>\n\t\t\t\t\t\t\t<b>{{ t('mediadc', 'Deleted: ') }} </b>\n\t\t\t\t\t\t\t{{ task.deleted_files_count }} {{ t('mediadc', 'file(s)') }}\n\t\t\t\t\t\t\t({{ formatBytes(Number(task.deleted_files_size)) }})\n\t\t\t\t\t\t</span>\n\t\t\t\t\t\t<span>\n\t\t\t\t\t\t\t{{ parseUnixTimestamp(task.created_time) }}\n\t\t\t\t\t\t\t{{ Number(task.finished_time) > 0 ? ' - ' + parseUnixTimestamp(task.finished_time) : '' }}\n\t\t\t\t\t\t</span>\n\t\t\t\t\t</div>\n\t\t\t\t\t<div class=\"app-content-list-menu\" style=\"margin: 0 0 0 10px; position: relative;\">\n\t\t\t\t\t\t<div class=\"icon-more actions-menu-button\" @click=\"openActionsPopup\" />\n\t\t\t\t\t\t<div :class=\"actionsOpened ? 'popovermenu open' : 'popovermenu'\"\n\t\t\t\t\t\t\tstyle=\"right: -1px;\">\n\t\t\t\t\t\t\t<ul>\n\t\t\t\t\t\t\t\t<li>\n\t\t\t\t\t\t\t\t\t<a class=\"icon-history\" @click=\"restartTask(task)\">\n\t\t\t\t\t\t\t\t\t\t<span>{{ t('mediadc', 'Restart') }}</span>\n\t\t\t\t\t\t\t\t\t</a>\n\t\t\t\t\t\t\t\t</li>\n\t\t\t\t\t\t\t\t<li>\n\t\t\t\t\t\t\t\t\t<a class=\"icon-rename\" @click=\"openEditTaskDialog(task)\">\n\t\t\t\t\t\t\t\t\t\t<span>{{ t('mediadc', 'Edit') }}</span>\n\t\t\t\t\t\t\t\t\t</a>\n\t\t\t\t\t\t\t\t</li>\n\t\t\t\t\t\t\t\t<li>\n\t\t\t\t\t\t\t\t\t<a class=\"icon-pause\" @click=\"terminateTask(task)\">\n\t\t\t\t\t\t\t\t\t\t<span>{{ t('mediadc', 'Stop') }}</span>\n\t\t\t\t\t\t\t\t\t</a>\n\t\t\t\t\t\t\t\t</li>\n\t\t\t\t\t\t\t\t<li>\n\t\t\t\t\t\t\t\t\t<a class=\"icon-delete\" @click=\"deleteTask(task)\">\n\t\t\t\t\t\t\t\t\t\t<span>{{ t('mediadc', 'Delete') }}</span>\n\t\t\t\t\t\t\t\t\t</a>\n\t\t\t\t\t\t\t\t</li>\n\t\t\t\t\t\t\t</ul>\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t\t<div class=\"task-info\">\n\t\t\t\t\t<h3>{{ t('mediadc', 'Target directories') }}</h3>\n\t\t\t\t\t<div class=\"target-directories-list\">\n\t\t\t\t\t\t<div v-for=\"dir in taskInfo.target_directories\" :key=\"dir.fileid\" class=\"target-directory-row\">\n\t\t\t\t\t\t\t<div class=\"owner-tooltip\">\n\t\t\t\t\t\t\t\t<div class=\"tooltip-title\">\n\t\t\t\t\t\t\t\t\t{{ t('mediadc', 'onwer:') }} {{ dir.fileowner }}\n\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t\t<div class=\"tooltip-content\">\n\t\t\t\t\t\t\t\t\t<b>{{ dir.filepath.replace(`/${dir.fileowner}/files`, '').replace(`/${currentUser}/files`, '') }}</b>\n\t\t\t\t\t\t\t\t\t({{ formatBytes(dir.filesize) }})\n\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t\t<div v-if=\"isValidUser\" class=\"details-row\">\n\t\t\t\t<DetailsList v-if=\"getStatusBadge(task) === 'finished'\"\n\t\t\t\t\t:filessize=\"filessize\"\n\t\t\t\t\t:filestotal=\"filestotal\" />\n\t\t\t</div>\n\t\t\t<div v-else>\n\t\t\t\t<p style=\"text-align: center;\">\n\t\t\t\t\t<b>{{ t('mediadc', 'You are not allowed to manage other user\\'s tasks.') }}</b>\n\t\t\t\t</p>\n\t\t\t</div>\n\t\t\t<div v-if=\"task.errors !== undefined && task.errors.length > 0\" class=\"errors\">\n\t\t\t\t<h3>{{ t('mediadc', 'Task errors') }}</h3>\n\t\t\t\t<div v-for=\"error in task.errors.split('\\\\n')\" :key=\"error\" class=\"error-row\">\n\t\t\t\t\t{{ error }}\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\nimport axios from '@nextcloud/axios'\nimport { mapGetters } from 'vuex'\nimport Formats from '../mixins/Formats'\nimport { generateUrl } from '@nextcloud/router'\nimport DetailsList from '../components/details/DetailsList'\nimport { showSuccess, showError, showWarning } from '@nextcloud/dialogs'\nimport Configure from '../mixins/Configure'\nimport { getCurrentUser } from '@nextcloud/auth'\nimport { subscribe, unsubscribe } from '@nextcloud/event-bus'\nimport TasksEdit from '../components/tasks/TasksEdit'\n\nexport default {\n\tname: 'CollectorDetails',\n\tcomponents: {\n\t\tDetailsList,\n\t\tTasksEdit,\n\t},\n\tmixins: [\n\t\tFormats,\n\t\tConfigure,\n\t],\n\tprops: {\n\t\trootTitle: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\tloading: {\n\t\t\ttype: Boolean,\n\t\t\trequired: true,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tupdater: null,\n\t\t\tstatsUpdater: null,\n\t\t\tfilessize: 0,\n\t\t\tfilestotal: 0,\n\t\t\tactionsOpened: false,\n\t\t\tcollapsedStatus: false,\n\t\t\teditingTask: false,\n\t\t}\n\t},\n\tcomputed: {\n\t\t...mapGetters([\n\t\t\t'task',\n\t\t\t'taskInfo',\n\t\t\t'details',\n\t\t\t'settingByName',\n\t\t]),\n\t\tisValidUser() {\n\t\t\treturn getCurrentUser().uid === this.task.owner\n\t\t},\n\t\tcurrentUser() {\n\t\t\treturn getCurrentUser().uid\n\t\t},\n\t},\n\tbeforeMount() {\n\t\tthis.$emit('update:loading', true)\n\t\tthis.getTaskDetails()\n\t\tthis.getTaskInfo()\n\t\tsubscribe('restartTask', () => {\n\t\t\tthis.getTaskInfo()\n\t\t\tthis.getTaskDetails()\n\t\t\tthis.filessize = 0\n\t\t\tthis.filestotal = 0\n\t\t})\n\t\tsubscribe('updateTaskInfo', this.getDetailFilesTotalSize)\n\t\tthis.updater = setInterval(this.getTaskDetails, 5000)\n\t},\n\tbeforeDestroy() {\n\t\tclearInterval(this.updater)\n\t\tunsubscribe('updateTaskInfo', this.getTaskInfo)\n\t\tunsubscribe('restartTask')\n\t},\n\tmethods: {\n\t\tterminateTask(task) {\n\t\t\tthis.toggleActionsPopup()\n\t\t\tif (this.isValidUser) {\n\t\t\t\tthis.terminating = true\n\t\t\t\taxios.post(generateUrl(`/apps/mediadc/api/v1/tasks/${task.id}/terminate`)).then(res => {\n\t\t\t\t\tshowSuccess(t('mediadc', 'Task terminated'))\n\t\t\t\t\tthis.terminating = false\n\t\t\t\t\tthis.getTaskDetails()\n\t\t\t\t})\n\t\t\t} else {\n\t\t\t\tshowWarning(t('mediadc', 'You are not allowed to terminate this task'))\n\t\t\t}\n\t\t},\n\t\trestartTask(task) {\n\t\t\tthis.toggleActionsPopup()\n\t\t\tif (this.isValidUser) {\n\t\t\t\tthis.restarting = true\n\t\t\t\tthis.getSettings().then(() => {\n\t\t\t\t\taxios.post(generateUrl('/apps/mediadc/api/v1/tasks/restart'), {\n\t\t\t\t\t\ttaskId: task.id,\n\t\t\t\t\t\ttargetDirectoryIds: task.target_directory_ids,\n\t\t\t\t\t\texcludeList: {\n\t\t\t\t\t\t\tuser: JSON.parse(task.exclude_list).user,\n\t\t\t\t\t\t\tadmin: JSON.parse(this.settingByName('exclude_list').value),\n\t\t\t\t\t\t},\n\t\t\t\t\t\tcollectorSettings: {\n\t\t\t\t\t\t\thashing_algorithm: JSON.parse(this.settingByName('hashing_algorithm').value) || 'dhash',\n\t\t\t\t\t\t\tsimilarity_threshold: Number(JSON.parse(this.task.collector_settings).similarity_threshold),\n\t\t\t\t\t\t\thash_size: Number(this.settingByName('hash_size').value) || 16,\n\t\t\t\t\t\t\ttarget_mtype: Number(JSON.parse(this.task.collector_settings).target_mtype),\n\t\t\t\t\t\t},\n\t\t\t\t\t}).then(res => {\n\t\t\t\t\t\tthis.restarting = false\n\t\t\t\t\t\tif (res.data.success) {\n\t\t\t\t\t\t\tshowSuccess(t('mediadc', 'Task successfully restarted with previous settings!'))\n\t\t\t\t\t\t\tthis.getTaskDetails()\n\t\t\t\t\t\t\tthis.filessize = 0\n\t\t\t\t\t\t\tthis.filestotal = 0\n\t\t\t\t\t\t} else if (res.data.limit) {\n\t\t\t\t\t\t\tshowWarning(t('mediadc', 'Running tasks limit exceed. Try again later.'))\n\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\tshowWarning(t('medaidc', 'Some error occured while running Collector Task. Try again.'))\n\t\t\t\t\t\t}\n\t\t\t\t\t}).catch(err => {\n\t\t\t\t\t\tthis.restarting = false\n\t\t\t\t\t\tconsole.debug(err)\n\t\t\t\t\t\tshowError('Some error occured while running Collector Task. Try again.')\n\t\t\t\t\t})\n\t\t\t\t})\n\t\t\t} else {\n\t\t\t\tshowWarning(t('mediadc', 'You are not allowed to restart this