seTaskStatus() {\n\t\t\tthis.collapsedStatus = !this.collapsedStatus\n\t\t},\n\t\topenEditTaskDialog() {\n\t\t\tthis.editingTask = true\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.container {\n\tpadding: 0 20px;\n\twidth: 100%;\n\tmax-width: 1440px;\n\tmargin: 0 auto;\n\tmax-height: 100%;\n}\n\n@media (min-width: 1920px) {\n\t.container {\n\t\tmax-width: 80vw;\n\t}\n}\n\nh2 {\n\tdisplay: inline-flex;\n\talign-items: center;\n\tjustify-content: center;\n\twidth: 100%;\n\tmargin: 20px auto;\n\ttext-align: center;\n}\n\n.collapse-task-status-btn {\n\tdisplay: inline-flex;\n\tcursor: pointer;\n\twidth: 16px;\n\theight: 16px;\n\tmargin: 0 10px;\n\tpadding: 20px;\n\tborder-radius: 50%;\n\tuser-select: none;\n}\n\n.task-details-heading p {\n\ttext-align: center;\n}\n\n.details-row {\n\tdisplay: flex;\n\tmargin: 0 10px 20px;\n}\n\n@media (max-width: 767px) {\n\t.details-row {\n\t\tflex-wrap: wrap;\n\t}\n\t.task-details-heading {\n\t\tflex-direction: column;\n\t}\n}\n\n.task-status-row {\n\tdisplay: flex;\n\tjustify-content: center;\n\talign-items: center;\n\tflex-wrap: wrap;\n}\n\n.task-status {\n\tdisplay: inline-flex;\n\talign-items: center;\n\tborder: 1px solid #dadada;\n\tborder-radius: 5px;\n\tmargin: 20px;\n\tpadding: 10px;\n}\n\n.actions-menu-button {\n\tpadding: 20px;\n\tborder-radius: 50%;\n\tcursor: pointer;\n\tuser-select: none;\n}\n\n.actions-menu-button:hover, .collapse-task-status-btn:hover {\n\tbackground-color: #eee;\n}\n\n.actions-menu-button:active, .collapse-task-status-btn:active {\n\tbackground-color: #ddd;\n}\n\nbody.theme--dark .actions-menu-button:hover, body.theme--dark .collapse-task-status-btn:hover {\n\tbackground-color: #727272;\n}\n\nbody.theme--dark .actions-menu-button:active, body.theme--dark .collapse-task-status-btn:active {\n\tbackground-color: #5b5b5b;\n}\n\nbody.theme--dark .task-status, body.theme--dark .task-info {\n\tborder-color: #717171;\n}\n\n.task-info {\n\tmargin: 20px 0;\n\tborder: 1px solid #dadada;\n\tborder-radius: 5px;\